<template>
  <div>
    <div class="uk-section uk-section-default">
      <div class="uk-container uk-flex uk-flex-center">
        <div class="uk-card uk-card-default uk-card-body uk-width-1-2@m">
          <h3 class="uk-card-title uk-text-center">EDIT FORM</h3>
          <form @submit.prevent="updateForm" class>
            <fieldset class="uk-fieldset">
               <div class="uk-margin">
                <input disabled
                  class="uk-input"
                  v-model="projectid"
                  required
                  type="text"
                  placeholder="Project ID"
                />
              </div>
              <div class="uk-margin">
                <input
                  class="uk-input"
                  v-model="type"
                  required
                  type="text"
                  placeholder="EIA Type"
                />
              </div>

              <!-- <div class="uk-margin">
                <input
                  class="uk-input"
                  v-model="type"
                  required
                  type="text"
                  placeholder="EIA Type"
                />
              </div> -->

              <div class="uk-margin">
                <input
                  class="uk-input"
                  v-model="project"
                  required
                  type="text"
                  placeholder="Project"
                />
              </div>

              <div class="uk-margin">
                <input
                  class="uk-input"
                  v-model="projectproponent"
                  required
                  type="text"
                  placeholder="Project Proponent"
                />
              </div>

              <div class="uk-margin">
                <input
                  class="uk-input"
                  v-model="district"
                  required
                  type="text"
                  placeholder="District"
                />
              </div>

              <div class="uk-margin">
                <input
                  class="uk-input"
                  v-model="wmz"
                  required
                  type="text"
                  placeholder="WMZ"
                />
              </div>

              <div class="uk-margin">
                <input
                  class="uk-input"
                  v-model="datereceived"
                  required
                  type="text"
                  placeholder="Date Received"
                />
              </div>

              <div class="uk-margin">
                <input
                  class="uk-input"
                  v-model="datereviewed"
                  required
                  type="text"
                  placeholder="Date Reviewed"
                />
              </div>

              <div class="uk-margin">
                <input
                  class="uk-input"
                  v-model="senttonema"
                  required
                  type="text"
                  placeholder="Sent to Nema"
                />
              </div>

            

              <!-- BUTTON START -->
              <div class="uk-text-center">
                <button class="uk-button uk-button-primary" type="submit">SAVE</button>
                &nbsp;
                <router-link to="/">
                  <button class="uk-button uk-button-secondary">CANCEL</button>
                </router-link>
              </div>

              <!-- BUTTON END -->
            </fieldset>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import db from "../firebase/firebaseInit";
export default {
  name: "Edit",
  data() {
    return {
      projectid: null,
      type: null,
      project: null,
      projectproponent: null,
      district: null,
      wmz: null,
      datereceived: null,
      datereviewed: null,
      senttonema: null
    };
  },

  beforeRouteEnter(to, from, next) {
    db.collection("documents")
      .where("projectid", "==", to.params.projectid)
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(charlie => {
            (charlie.projectid = doc.data().projectid),
              (charlie.type = doc.data().type),
              (charlie.project = doc.data().project),
              (charlie.projectproponent = doc.data().project),
              (charlie.district = doc.data().district),
              (charlie.wmz = doc.data().wmz),
              (charlie.datereceived = doc.data().datereceived),
              (charlie.datereviewed = doc.data().datereviewed),
              (charlie.senttonema = doc.data().senttonema);
          });
        });
      });
  },
  watch: {
    $route: "fetchData"
  },
  methods: {
    fetchData() {
      db.collection("documents")
        .where("projectid", "==", this.$route.params.projectid)
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.projectid= doc.data().projectid,
            this.type= doc.data().type,
            this.project= doc.data().project,
            this.projectproponent= doc.data().projectproponent,
            this.district= doc.data().district,
            this.wmz= doc.data().wmz,
            this.datereceived= doc.data().datereceived,
            this.datereviewed= doc.data().datereviewed,
            this.senttonema= doc.data().senttonema
          });
        });
    },
    updateForm() {
      db.collection("documents")
        .where("projectid", "==", this.$route.params.projectid)
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            doc.ref
              .update({
                projectid: this.projectid,
                type: this.type,
                project: this.project,
                projectproponent: this.projectproponent,
                district: this.district,
                wmz: this.wmz,
                datereceived: this.datereceived,
                datereviewed: this.datereviewed,
                senttonema: this.senttonema
              })
              .then(() =>
                this.$router.push({
                  name: "View",
                  params: { projectid: this.projectid }
                }),
                alert("Saved Successfully")
              );
          });
        });
    }
  }
};
</script>