<template>
  <div>
    <div class="uk-section uk-section-default">
      <div class="uk-container uk-flex uk-flex-center">
        <div class="uk-card uk-card-default uk-card-body uk-width-1-2@m">
          <h3 class="uk-card-title uk-text-center">CREATE NEW FORM</h3>
          <form @submit.prevent="saveDocument" class>
            <fieldset class="uk-fieldset">
              <div class="uk-margin">
                <input
                  class="uk-input"
                  v-model="projectid"
                  required
                  type="text"
                  placeholder="Project ID"
                />
              </div>
              <div class="uk-margin">
                <input
                  class="uk-input"
                  v-model="type"
                  required
                  type="text"
                  placeholder="EIA Type"
                />
              </div>

              <!-- <div class="uk-margin">
                <input
                  class="uk-input"
                  v-model="type"
                  required
                  type="text"
                  placeholder="EIA Type"
                />
              </div> -->

              <div class="uk-margin">
                <input
                  class="uk-input"
                  v-model="project"
                  required
                  type="text"
                  placeholder="Project"
                />
              </div>

              <div class="uk-margin">
                <input
                  class="uk-input"
                  v-model="projectproponent"
                  required
                  type="text"
                  placeholder="Project Proponent"
                />
              </div>

              <div class="uk-margin">
                <input
                  class="uk-input"
                  v-model="district"
                  required
                  type="text"
                  placeholder="District"
                />
              </div>

              <div class="uk-margin">
                <input
                  class="uk-input"
                  v-model="wmz"
                  required
                  type="text"
                  placeholder="WMZ"
                />
              </div>

              <div class="uk-margin">
                <input
                  class="uk-input"
                  v-model="datereceived"
                  required
                  type="text"
                  placeholder="Date Received"
                />
              </div>

              <div class="uk-margin">
                <input
                  class="uk-input"
                  v-model="datereviewed"
                  required
                  type="text"
                  placeholder="Date Reviewed"

                />
              </div>

              <div class="uk-margin">
                <input
                  class="uk-input"
                  v-model="senttonema"
                  required
                  type="text"
                  placeholder="Sent to Nema"
                />
              </div>


     <!-- project: this.project,
          projectproponent: this.projectproponent,
          district: this.district,
          wmz: this.wmz,
          datereceived: this.datereceived,
          datereviewed: this.datereviewed,
          senttonema: this.senttonema -->
              <!-- BUTTON START -->
              <div class="uk-text-center">
                <button class="uk-button uk-button-primary" type="submit">SAVE</button>
                
               
                	&nbsp;
                <router-link to="/" ><button class="uk-button uk-button-secondary">CANCEL</button></router-link>
              </div>

              <!-- BUTTON END -->
            </fieldset>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import db from "../firebase/firebaseInit";
export default {
  name: "Add",
  data() {
    return {
      projectid: null,
      type: null,
      project: null,
      projectproponent: null,
      district: null,
      wmz: null,
      datereceived: null,
      datereviewed: null,
      senttonema: null
    };
  },
  methods: {
    saveDocument() {
      db.collection("documents")
        .add({
          projectid: this.projectid,
          type: this.type,
          project: this.project,
          projectproponent: this.projectproponent,
          district: this.district,
          wmz: this.wmz,
          datereceived: this.datereceived,
          datereviewed: this.datereviewed,
          senttonema: this.senttonema
        })
        .then(() => {
         
          this.$router.push("/")
           alert("Saved Successfully")
          ;
        })
        .catch(() => console.log("There is an Error"));
    }
  }
};
</script>