<template>
  <div>
    <div class="uk-section uk-section-default">
      <div class="uk-container uk-flex uk-flex-center">
        <div class="uk-card uk-card-default uk-card-body uk-width-1-2@m">
          <h3 class="uk-card-title uk-text-center">Project Details</h3>
          <form>
            <fieldset class="uk-fieldset">
              <!-- BUTTON START -->
              <div class="uk-text-center">
                <router-link v-bind:to="{name: 'Edit', params:{projectid:projectid}}">
                  <button class="uk-button uk-button-primary">EDIT</button>
                </router-link>
                &nbsp;
                <a class="uk-button uk-button-secondary" @click="deleteDocument">DELETE</a>
              </div>

              <!-- BUTTON END -->

              <div class="uk-margin">
                <label class="uk-form-label uk-text-primary" for="form-stacked-text">EIA Number</label>
                <div class="uk-h4 uk-margin-remove">{{projectid}}</div>
              </div>
              <div class="uk-margin">
                <label class="uk-form-label uk-text-primary" for="form-stacked-text">EIA Type</label>
                <div class="uk-h4 uk-margin-remove">{{type}}</div>
              </div>

              <div class="uk-margin">
                <label class="uk-form-label uk-text-primary" for="form-stacked-text">Project</label>
                <div class="uk-h4 uk-margin-remove">{{project}}</div>
              </div>

              <div class="uk-margin">
                <label
                  class="uk-form-label uk-text-primary"
                  for="form-stacked-text"
                >Project Proponent</label>
                <div class="uk-h4 uk-margin-remove">{{projectproponent}}</div>
              </div>

              <div class="uk-margin">
                <label class="uk-form-label uk-text-primary" for="form-stacked-text">District</label>
                <div class="uk-h4 uk-margin-remove">{{district}}</div>
              </div>
              <div class="uk-margin">
                <label class="uk-form-label uk-text-primary" for="form-stacked-text">WMZ</label>
                <div class="uk-h4 uk-margin-remove">{{wmz}}</div>
              </div>
              <div class="uk-margin">
                <label class="uk-form-label uk-text-primary" for="form-stacked-text">Date Receieved</label>
                <div class="uk-h4 uk-margin-remove">{{datereceived}}</div>
              </div>
              <div class="uk-margin">
                <label class="uk-form-label uk-text-primary" for="form-stacked-text">Date REVIEWED</label>
                <div class="uk-h4 uk-margin-remove">{{datereviewed}}</div>
              </div>
              <div class="uk-margin">
                <label class="uk-form-label uk-text-primary" for="form-stacked-text">Sent to Nema</label>
                <div class="uk-h4 uk-margin-remove">{{senttonema}}</div>
              </div>

              <!-- projectid: null,
              type: null,
              project: null,
              projectproponent: null,
              district: null,
              wmz: null,
              datereceived: null,
              datereviewed: null,
              senttonema: null-->
              <!-- <div class="uk-margin">
                <label class="uk-form-label" for="form-stacked-text">Project Proponent</label>
                <div class="uk-h4">McALESTER Energy Resources Limited</div>
              </div>

              <div class="uk-margin">
                <label class="uk-form-label" for="form-stacked-text">District</label>
                <div class="uk-h4">Hoima</div>
              </div>
              <div class="uk-margin">
                <label class="uk-form-label" for="form-stacked-text">WMZ</label>
                <div class="uk-h4">Albert WMZ</div>
              </div>
              <div class="uk-margin">
                <label class="uk-form-label" for="form-stacked-text">Date Receieved</label>
                <div class="uk-h4">19/10/2012</div>
              </div>

              <div class="uk-margin">
                <label class="uk-form-label" for="form-stacked-text">Date Reviewed</label>
                <div class="uk-h4">22/10/2012</div>
              </div>

              <div class="uk-margin">
                <label class="uk-form-label" for="form-stacked-text">Evalutaion</label>
                <div class="uk-h4">Sent</div>
              </div>-->
            </fieldset>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>
 

<script>
import db from "../firebase/firebaseInit";
export default {
  name: "View",
  data() {
    return {
      projectid: null,
      type: null,
      project: null,
      projectproponent: null,
      district: null,
      wmz: null,
      datereceived: null,
      datereviewed: null,
      senttonema: null
    };
  },
  beforeRouteEnter(to, from, next) {
    db.collection("documents")
      .where("projectid", "==", to.params.projectid)
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(xray => {
            (xray.projectid = doc.data().projectid),
              (xray.type = doc.data().type),
              (xray.project = doc.data().project),
              (xray.projectproponent = doc.data().project),
              (xray.district = doc.data().district),
              (xray.wmz = doc.data().wmz),
              (xray.datereceived = doc.data().datereceived),
              (xray.datereviewed = doc.data().datereviewed),
              (xray.senttonema = doc.data().senttonema);
          });
        });
      });
  },
  watch: {
    $route: "fetchData"
  },
  methods: {
    fetchData() {
      db.collection("documents")
        .where("projectid", "==", this.$route.params.projectid)
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.projectid = doc.data().projectid;
            this.project = doc.data().project;
          });
        });
    },
    deleteDocument() {
      if (
        confirm("Are you sure you want to permanently delete this document?")
      ) {
        db.collection("documents")
          .where("projectid", "==", this.$route.params.projectid)
          .get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
              doc.ref.delete();
              this.$router.push("/");
            });
          });
      }
    }
  }
};
</script>