<template>
  <div>
    <div class="uk-section uk-section-default">
      <div class="uk-container">
        <div>
          <router-link v-bind:to="{name: 'Add'}" @click="navigate">
            <button class="uk-button uk-button-primary">CREATE</button>
          </router-link>

          <!-- <button class="uk-button uk-button-danger">DELETE</button>
          <button class="uk-button uk-button-primary">EDIT</button>-->
        </div>

        <div class="uk-overflow-auto">
          <table class="uk-table uk-table-hover uk-table-middle uk-table-divider">
            <thead>
              <tr>
                <th class="uk-table-shrink">
                  <input class="uk-checkbox" type="checkbox" />
                </th>
                <th class="uk-table-expand uk-text-nowrap">PROJECT ID</th>
                <th class="uk-table-expand uk-text-nowrap">TYPE</th>
                <th class="uk-table-expand uk-text-nowrap">PROJECT</th>
                <th class="uk-table-expand uk-text-nowrap">PROJECT PROPONENT</th>
                <th class="uk-table-expand uk-text-nowrap">DISTRICT</th>
                <th class="uk-table-expand uk-text-nowrap">WMZ</th>
                <th class="uk-table-expand uk-text-nowrap">DATE RECEIVED</th>
                <th class="uk-table-expand uk-text-nowrap">DATE REVIEWED</th>
                <th class="uk-table-expand uk-text-nowrap">SENT TO NEMA?</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="record in records" v-bind:key="record.id">
                <td>
                  <input class="uk-checkbox" type="checkbox" />
                </td>
                <td class="uk-table-link uk-text-truncate">
                  <router-link
                    class="uk-link-reset"
                    v-bind:to="{name: 'View',params: {projectid: record.projectid}}"
                  >{{record.projectid}}</router-link>
                </td>
                <td class="uk-table-link uk-text-truncate">
                  <router-link
                    class="uk-link-reset"
                    v-bind:to="{name: 'View',params: {projectid: record.projectid}}"
                  >{{record.type}}</router-link>
                </td>
                <td class="uk-table-link uk-text-truncate">
                  <router-link
                    class="uk-link-reset"
                    v-bind:to="{name: 'View',params: {projectid: record.projectid}}"
                  >{{record.project}}</router-link>
                </td>

                <td class="uk-text-nowrap">{{record.projectproponent}}</td>
                <td class="uk-text-nowrap">{{record.district}}</td>
                <td class="uk-text-nowrap">{{record.wmz}}</td>
                <td class="uk-text-nowrap">{{record.datereceived}}</td>
                <td class="uk-text-nowrap">{{record.datereviewed}}</td>
                <td class="uk-text-nowrap">{{record.senttonema}}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import db from "../firebase/firebaseInit";
export default {
  name: "home",
  data() {
    return {
      records: []
    };
  },
  created() {
    db.collection("documents")
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          //console.log(doc.data());
          const data = {
            id: doc.id,
            projectid: doc.data().projectid,
            type: doc.data().type,
            project: doc.data().project,
            projectproponent: doc.data().projectproponent,
            district: doc.data().district,
            wmz: doc.data().wmz,
            datereceived: doc.data().datereceived,
            datereviewed: doc.data().datereviewed,
            senttonema: doc.data().senttonema
          };
          this.records.push(data);
        });
      });
  }
};
</script>